import { Component, OnInit } from '@angular/core';
import { AdminUser } from '../shared/model';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { AccountService } from '../service/account.service';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { StorageService } from '../service/storage.service';

@Component({
  selector: 'app-account',
  templateUrl: './account.component.html',
  styleUrls: ['./account.component.css']
})
export class AccountComponent implements OnInit {

  user: AdminUser = new AdminUser();

  constructor(private router: Router, private toastr: ToastrService,
    private account: AccountService,
       private spinnerService: Ng4LoadingSpinnerService,
        private storage: StorageService) { }

  ngOnInit() {

  }

  public Ok(model: AdminUser) {

     const self = this;

     if (model === null) {
      self.toastr.error('User model cannot be empty ');
     }
   console.log( model.Role);
    this.account.CreateUser(model).subscribe(c => {

      if (c.Succeeded)  {
        self.toastr.success('Operation successful');
      } else {
        self.toastr.error('Operation failed ' + c.Message);
      }
     }, e => {
      self.toastr.error('Error in operation ' + e);
    });
  }

}
