import { Component, OnInit, AfterViewInit, Inject } from '@angular/core';
import { Router } from '@angular/router';
import { StorageService } from '../service/storage.service';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { ToastrService } from 'ngx-toastr';
import { Constants } from '../shared/Constants';
import * as $ from 'jquery';
import { ReportService } from '../service/report.service';
import { RecordStatus, WithdrawalResponse } from '../shared/model';
import { Observable } from 'rxjs';
import { SequencePage } from '../shared/sequencepage';
import { WithdrawalService } from '../service/withdrawal.service';

@Component({
  selector: 'app-approvedwithdrawal',
  templateUrl: './approvedwithdrawal.component.html',
  // styleUrls: ['./customer.component.css']
})
export class ApprovedWithdrawalComponent implements OnInit {

  baseurl: string = location.protocol + '//' + location.host + '/';
  public data: SequencePage<WithdrawalResponse>;
  public response: WithdrawalResponse[] = new Array<WithdrawalResponse>();
  public pageIndex = 1;
  public pageSize = 100;
  public pageLinks: number[] = [];
  public hasPage: boolean;

  constructor(private router: Router,
    private toastr: ToastrService, private storage: StorageService,
    private spinnerService: Ng4LoadingSpinnerService, private service: WithdrawalService) {

  }

  ngOnInit() {
    this.LoadFirstPage();
  }

  GetStatus(status: RecordStatus) {

  }
    LoadFirstPage() {
      const get = this.Get(0);
      this.hasPage = this.HasNoPage();
      return get;
    }

    LoadLastPage() {
      if (this.data) {
        return this.Get((Math.ceil(this.data.SequenceLength / this.pageSize) - 1));
      }
    }

    HasNoPage(): boolean {
      const result: boolean = this.data == null
      || typeof this.data === 'undefined'
      || this.data.Page.length === 0;

      return result;
    }

    public Get(pageIndex?: number): Array<WithdrawalResponse> {
      const self = this;

      self.service.getApproveWithdrawal(pageIndex, this.pageSize).subscribe(resp => {
        const _page = resp.Result;

        if (_page.PageSize === 0) {
          // <-- debug stuff
          self.pageLinks = [];
        } else {
          self.data = new SequencePage<WithdrawalResponse>(_page.Page, _page.SequenceLength, _page.PageSize, _page.PageIndex);
          self.pageLinks = self.data.AdjacentIndexes(2);
          self.response = self.data.Page;
        }
        return self.response;
      }, err => {
        self.data = new SequencePage<WithdrawalResponse>([], 0);
        self.pageLinks = self.data.AdjacentIndexes(2);
        return self.response;
      });
      return self.response;
    }
    public GetWithdrawalStatus(status: number): string {
      if (status === 0) {
        return 'Pending';
      } else if (status === 1) {
        return 'Approved';
       } else if (status === 2) {
        return 'Paid';
      }
    }

    public ViewCustomer(id: number) {


    }
}
