import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute  } from '@angular/router';
import { StorageService } from '../service/storage.service';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { ToastrService } from 'ngx-toastr';
import { RecordStatus, WithdrawalResponse, WithdrawApprovalModel, AgentUserAdmin } from '../shared/model';
import { SequencePage } from '../shared/sequencepage';
import { WithdrawalService } from '../service/withdrawal.service';
import { switchMap } from 'rxjs/operators';

@Component({
  selector: 'app-withdrawalagent',
  templateUrl: './withdrawalagent.component.html',
  // styleUrls: ['./customer.component.css']
})
export class WithdrawalAgentComponent implements OnInit {

  baseurl: string = location.protocol + '//' + location.host + '/';
  public data: SequencePage<WithdrawalResponse>;
  public response: WithdrawalResponse[] = new Array<WithdrawalResponse>();
  public pageIndex = 1;
  public pageSize = 100;
  public pageLinks: number[] = [];
  public hasPage: boolean;
  public withd: WithdrawApprovalModel = new WithdrawApprovalModel();
   public withdrawalId: number;
   public agentUserAdmins: AgentUserAdmin[] = new Array<AgentUserAdmin>();

  constructor(private router: Router, private route: ActivatedRoute,
    private toastr: ToastrService, private storage: StorageService,
    private spinnerService: Ng4LoadingSpinnerService, private service: WithdrawalService) {

      const wid = this.route.snapshot.paramMap.get('id');

      if (wid !== null) {
          this.withdrawalId = Number(wid);
      }
  }

  ngOnInit() {
     const self = this;

    this.service.GetSystemAdmin().subscribe(c => {

       if (c.Succeeded) {
        self.agentUserAdmins = c.Result;
       }

    });
  }

    public GetWithdrawalStatus(status: number): string {
      if (status === 0) {
        return 'Pending';
      } else if (status === 1) {
        return 'Approved';
       } else if (status === 2) {
        return 'Paid';
      }
    }

    public Ok(model: WithdrawApprovalModel) {

    }
    public ShowApprovalForm(id: number) {
     // const wid = this.route.snapshot.paramMap.get('id');

     this.router.navigate(['/withdrawalagent', id]);
    }
    public ApproveWithdrawal(model: AgentUserAdmin ) {

     const self = this;
     this.withd.WithdrawalId = this.withdrawalId;

     this.service.WithdrawalApproval(this.withd).subscribe(c => {

   if (c.Succeeded) {
    self.toastr.success('Operation Successful');
   } else {
     self.toastr.error(c.Message);
   }
      }, e => {
      self.toastr.error('Operation failed ' + e);
      });
     }
}
