import { Component, OnInit, AfterViewInit, Inject } from '@angular/core';
import { Router , ActivatedRoute } from '@angular/router';
import { StorageService } from '../service/storage.service';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { ToastrService } from 'ngx-toastr';
import { Constants } from '../shared/Constants';
import * as $ from 'jquery';
import { ReportService } from '../service/report.service';
import { Dashboard, DashboardModel, Customer, RecordStatus } from '../shared/model';
import { Observable } from 'rxjs';
import { SequencePage } from '../shared/sequencepage';
import { CustomerService } from '../service/customer.service';

@Component({
  selector: 'app-viewcustomer',
  templateUrl: './viewcustomer.component.html',
  // styleUrls: ['./customer.component.css']
})
export class ViewCustomerComponent implements OnInit {

  baseurl: string = location.protocol + '//' + location.host + '/';
  recordStatus: RecordStatus;
  dashboard: DashboardModel = new DashboardModel();

  public data: SequencePage<Customer>;
  public customers: Customer[] = new Array<Customer>();
  public pageIndex = 1;
  public pageSize = 100;
  public pageLinks: number[] = [];
  public hasPage: boolean;
  public customer: Customer = new Customer();

  constructor(private router: Router, private route: ActivatedRoute,
    private toastr: ToastrService, private storage: StorageService,
    private spinnerService: Ng4LoadingSpinnerService, private service: CustomerService) {
      const self = this;
      const cid = this.route.snapshot.paramMap.get('id');

      if (cid !== null) {

    this.service.GetCustomer(Number(cid)).subscribe(c => {
     if (c.Succeeded) {
       self.customer = c.Result;
     } else {
       self.toastr.error(c.Message, 'Error ');
     }
  }, e => {
   self.toastr.error('Operation failed ' + e , 'Error' );
  });
}

  }

  ngOnInit() {


  }

  GetStatus(status: RecordStatus) {

  }

   public onUpdate(id: number) {
    this.toastr.success(`Customer with id has been updated`);
   }

}
