import { Component, OnInit, AfterViewInit, Inject , ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { StorageService } from '../service/storage.service';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { ToastrService } from 'ngx-toastr';
import {  TransactionLogger } from '../shared/model';
import { Observable } from 'rxjs';
import { TransactionService } from '../service/transaction.service';
// import {CreateNewAutocompleteGroup, SelectedAutocompleteItem, NgAutocompleteComponent} from 'ng-auto-complete';
import { FormGroup, FormBuilder, FormArray } from '@angular/forms';

@Component({
  selector: 'app-createtransaction',
  templateUrl: './createtransaction.component.html',
  styleUrls: ['./createtransaction.component.css']
})
export class CreateTransactionComponent implements OnInit {

  // @ViewChild(NgAutocompleteComponent) public completer: NgAutocompleteComponent;

   baseurl: string = location.protocol + '//' + location.host + '/';

   transaction: TransactionLogger = new TransactionLogger();
  CustomerName: string;

  constructor(private router: Router,
    private toastr: ToastrService, private storage: StorageService,
    private spinnerService: Ng4LoadingSpinnerService, private service: TransactionService) {

  }

  ngOnInit() {

  }


 public Search(search: string) {

  this.CustomerName = '';
  if (search ===  null || search === undefined) {
    this.toastr.error('customer code cannot be empty', 'Error Message');
  }
  const self = this;
  this.service.getCustomerNameByCode(search).subscribe(c => {
    if (c.Succeeded) {
      self.CustomerName = c.Result;
    } else  {
      self.CustomerName = null;
      self.transaction.CustomerCode = null;
     self.toastr.error('Customer information not found', 'Error Message');

    }

  }, e => {
    self.CustomerName = null;
    self.transaction.CustomerCode = null;
   self.toastr.error('Customer information not found', 'Error Message');

  } );

 }

 public Ok(model: TransactionLogger) {

   if (model == null) {
     this.toastr.error('transaction posting cannot be empty', 'Error Message');
     return;
   }
   if (model.Amount <= 0) {
    this.toastr.error('transaction amount cannot be less than zero', 'Error Message');
    return;
   }
   if (model.CustomerCode == null || model.CustomerCode === '') {
    this.toastr.error('Customer code amount cannot be empty', 'Error Message');
    return;
   }
    const  self = this;
   this.service.PostTransactionLog(model).subscribe(c => {

    if (c.Succeeded) {
      model.Amount = 0;
      model.CustomerCode = '';
    self.toastr.success('Operation successful', 'Success');
    } else {
      model.Amount = 0;
      model.CustomerCode = '';
      self.toastr.error('Operation failed ', 'Error Message');
    }

   }, e => {

      model.Amount = 0;
      model.CustomerCode = '';
      self.toastr.error('Transaction failed to create', 'Error Message');
   });
  }



}
