import { Injectable } from '@angular/core';
import { User, Token, UserLogin, Operation, PasswordChange, ResponseResource, UserResource, Customer, SearchModel } from '../shared/model';
import { DataService } from './data.service';
import { StorageService } from './storage.service';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { SequencePage } from '../shared/sequencepage';

@Injectable({
  providedIn: 'root'
})

export class CustomerService {

  constructor(private data: DataService, private storage: StorageService) {
  }
   public getCustomerSeach(model: SearchModel): Observable<Operation<SequencePage<Customer>>> {
    const url = `/api/v1/customer/records`;
    const result = this.data.Post<SequencePage<Customer>>(url, model);
    return result;
   }
  public getCustomers(pageIndex: number, pageSize: number): Observable<Operation<SequencePage<Customer>>> {

    const url = `/api/v1/customer/${pageIndex}/${pageSize}`;
    const result = this.data.Get<SequencePage<Customer>>(url);
    return result;
  }

  public getActiveCustomers(pageIndex: number, pageSize: number): Observable<Operation<SequencePage<Customer>>> {

    const url = `/api/v1/customer/${pageIndex}/active/${pageSize}`;

    const result = this.data.Get<SequencePage<Customer>>(url);
    return result;
  }
  public getInActiveCustomers(pageIndex: number, pageSize: number): Observable<Operation<SequencePage<Customer>>> {

    const url = `/api/v1/customer/${pageIndex}/in-active/${pageSize}`;

    const result = this.data.Get<SequencePage<Customer>>(url);
    return result;
  }

  public CreateCustomer(model: Customer): Observable<Operation<Customer>> {

    const url = `/api/v1/Customer/registration-web`;

    const result = this.data.Post<Customer>(url, model);
    return result;
  }


  public resetCustomerBalance(id: number): Observable<Operation<void>> {

    const url = `/api/v1/customer/reset-customer/${id}/balance`;

    const result = this.data.Get<void>(url);
    return result;
  }

  public GetCustomer(id: number): Observable<Operation<Customer>> {

    const url = `/api/v1/customer/${id}/item`;
    const result = this.data.Get<Customer>(url);
    return result;
  }

public UpdateCustomer(model: Customer): Observable<Operation<Customer>> {

  const url = `/api/v1/customer/update`;
  const result = this.data.Put<Customer>(url, model);
  return result;
}

}


