import { Component, OnInit, AfterViewInit, Inject } from "@angular/core";
import { Router } from "@angular/router";
import { StorageService } from "../service/storage.service";
import { Ng4LoadingSpinnerService } from "ng4-loading-spinner";
import { ToastrService } from "ngx-toastr";
import { Constants } from "../shared/Constants";
import * as $ from "jquery";
import { ReportService } from "../service/report.service";
import {
  Dashboard,
  DashboardModel,
  Customer,
  RecordStatus,
  SearchModel
} from "../shared/model";
import { Observable } from "rxjs";
import { SequencePage } from "../shared/sequencepage";
import { CustomerService } from "../service/customer.service";

@Component({
  selector: "app-customer",
  templateUrl: "./customer.component.html"
  // styleUrls: ['./customer.component.css']
})
export class CustomerComponent implements OnInit {
  baseurl: string = location.protocol + "//" + location.host + "/";
  recordStatus: RecordStatus;
  dashboard: DashboardModel = new DashboardModel();

  public data: SequencePage<Customer>;
  public customers: Customer[] = new Array<Customer>();
  public pageIndex = 1;
  public pageSize = 100;
  public pageLinks: number[] = [];
  public hasPage: boolean;
  public roleName: string;

  public customer: Customer;
  public searchModel: SearchModel = new SearchModel();

  constructor(
    private router: Router,
    private toastr: ToastrService,
    private storage: StorageService,
    private spinnerService: Ng4LoadingSpinnerService,
    private service: CustomerService
  ) {}

  public reloadPage() {
    document.location.reload();
  }

  ngOnInit() {
    this.roleName = this.storage.GetUserInfo().Role;
    this.LoadFirstPage();
  }

  GetStatus(status: RecordStatus) {}
  LoadFirstPage() {
    const get = this.Get(0);
    this.hasPage = this.HasNoPage();
    return get;
  }

  LoadLastPage() {
    if (this.data) {
      return this.Get(Math.ceil(this.data.SequenceLength / this.pageSize) - 1);
    }
  }

  HasNoPage(): boolean {
    const result: boolean =
      this.data == null ||
      typeof this.data === "undefined" ||
      this.data.Page.length === 0;

    return result;
  }

  public SearchCustomer(searchMod: SearchModel) {
    const self = this;

    self.service.getCustomerSeach(searchMod).subscribe(
      resp => {
        const _page = resp.Result;
        if (_page.PageSize === 0) {
          // <-- debug stuff
          self.pageLinks = [];
        } else {
          self.data = new SequencePage<Customer>(
            _page.Page,
            _page.SequenceLength,
            _page.PageSize,
            _page.PageIndex
          );
          self.pageLinks = self.data.AdjacentIndexes(2);
          self.customers = self.data.Page;
        }
        return self.customers;
      },
      err => {
        self.data = new SequencePage<Customer>([], 0);
        self.pageLinks = self.data.AdjacentIndexes(2);
        return self.customers;
      }
    );
    return self.customers;
  }
  public Get(pageIndex?: number): Array<Customer> {
    const self = this;
    this.searchModel.pageSize = this.pageSize;
    this.searchModel.pageIndex = pageIndex;

    self.service.getCustomerSeach(this.searchModel).subscribe(
      resp => {
        const _page = resp.Result;
        if (_page.PageSize === 0) {
          // <-- debug stuff
          self.pageLinks = [];
        } else {
          self.data = new SequencePage<Customer>(
            _page.Page,
            _page.SequenceLength,
            _page.PageSize,
            _page.PageIndex
          );
          self.pageLinks = self.data.AdjacentIndexes(2);
          self.customers = self.data.Page;
        }
        return self.customers;
      },
      err => {
        self.data = new SequencePage<Customer>([], 0);
        self.pageLinks = self.data.AdjacentIndexes(2);
        return self.customers;
      }
    );
    return self.customers;
  }

  public ResetBalance(id: number) {
    const self = this;
    if (!confirm("Are you sure")) {
      self.toastr.success(
        "Customer balance reset operation cancelled successfully",
        "Success"
      );
      return;
    }
    this.service.resetCustomerBalance(id).subscribe(c => {
      if (c.Succeeded) {
        self.toastr.success("Operation Succesful", "Success");
        this.LoadFirstPage();
      } else {
        self.toastr.error(c.Message, "Error");
      }
    });
  }
  public onUpdate(id: number) {
    this.toastr.success(`Customer with id has been updated`);
  }

  public GetCustomerView(id: number) {
    this.router.navigate(["/customer-view", id]);
  }
  public GetCustomerUpdate(id: number) {
    this.router.navigate(["/customer-update", id]);
  }
}
