export class TransactionResponse extends Model {
  public Id: number;
  public TransactionNo: string;
  public Amount: number;
  public TransactionType: string; // DR or CR
  public TransactionCode: string;
  public CustomerId: number;
  public MerchantId: number;
  public Merchant: Merchant;
  public Customer: Customer;
  public IsNotified: boolean;
  public IsService: boolean;
  public CollectionTypeId: number;
  public TransactionMessage: string;
  public TechFeeStartDate: Date;
  public TechFeeEndDate: Date;
  public TransactionMapCode: string;
  public Agent: AgentModel;
  public UserName: string;
  public MerchantName: string;
  public AgentName: string;
  public IsApproved: boolean;
  private _CustomerCode: string;
  public get CustomerCode(): string {
    return this._CustomerCode;
  }
  public set CustomerCode(value: string) {
    this._CustomerCode = value;
  }
}

