import { Injectable } from '@angular/core';
import {  Operation, WithdrawalResponse, WithdrawalLogger, WithdrawApprovalModel, AgentUserAdmin, SearchModel } from '../shared/model';
import { DataService } from './data.service';
import { StorageService } from './storage.service';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { SequencePage } from '../shared/sequencepage';

@Injectable({
  providedIn: 'root'
})

export class WithdrawalService {

  constructor(private data: DataService, private storage: StorageService) {
  }

  public getWithdrawalSearch(model: SearchModel): Observable<Operation<SequencePage<WithdrawalResponse>>> {

    const url = `/api/v1/withdrawal/records`;
    const result = this.data.Post<SequencePage<WithdrawalResponse>>(url, model);

    return result;
  }
  // Get withdrawal
  public getWithdrawal(pageIndex: number, pageSize: number): Observable<Operation<SequencePage<WithdrawalResponse>>> {

    const url = `/api/v1/withdrawal/${pageIndex}/${pageSize}`;
    const result = this.data.Get<SequencePage<WithdrawalResponse>>(url);

    return result;
  }

  public getApproveWithdrawal(pageIndex: number, pageSize: number): Observable<Operation<SequencePage<WithdrawalResponse>>> {

    const url = `/api/v1/withdrawal/${pageIndex}/approved/${pageSize}`;
    const result = this.data.Get<SequencePage<WithdrawalResponse>>(url);

    return result;
  }
  public getPaidWithdrawal(pageIndex: number, pageSize: number): Observable<Operation<SequencePage<WithdrawalResponse>>> {

    const url = `/api/v1/withdrawal/${pageIndex}/paid/${pageSize}`;
    const result = this.data.Get<SequencePage<WithdrawalResponse>>(url);

    return result;
  }

  public getCustomerNameByCode(search: string): Observable<Operation<string>> {

    const url = `/api/v1/customer/${search}`;

    const result = this.data.Get<string>(url);
    return result;
  }
  public PostWithdrawal(model: WithdrawalLogger): Observable<Operation<WithdrawalLogger>>  {

    const url = `/api/v1/withdrawal/web`;
    const result = this.data.Post<WithdrawalLogger>(url, model);

    return result;
  }

  public WithdrawalApproval(model: WithdrawApprovalModel): Observable<Operation<WithdrawalLogger>>  {

    const url = `/api/v1/withdrawal/approval/admin`;
    const result = this.data.Post<WithdrawalLogger>(url, model);

    return result;
  }

  public WithdrawalDecline(id: number): Observable<Operation<void>>  {

    const url = `/api/v1/withdrawal/decline/${id}`;

    const result = this.data.Get<void>(url);

    return result;
  }

  public GetSystemAdmin(): Observable<Operation<AgentUserAdmin[]>> {

    const url = `/api/v1/account/agent-names`;
    const result = this.data.Get<AgentUserAdmin[]>(url);

    return result;
  }
}
