import { Component, OnInit } from "@angular/core";
import { UserLogin, Token } from "../shared/model";
import { Router } from "@angular/router";
import { ToastrService } from "ngx-toastr";
import { Ng4LoadingSpinnerService } from "ng4-loading-spinner";
import { StorageService } from "../service/storage.service";
import { Constants } from "../shared/Constants";
import { AccountService } from "../service/account.service";

@Component({
  selector: "app-login",
  templateUrl: "./login.component.html",
  styleUrls: ["./login.component.css"]
})
export class LoginComponent implements OnInit {
  login: UserLogin = new UserLogin();
  constructor(
    private router: Router,
    private toastr: ToastrService,
    private account: AccountService,
    private spinnerService: Ng4LoadingSpinnerService,
    private storage: StorageService
  ) {}

  ngOnInit() {}

  public onlogin(login: UserLogin) {
    this.spinnerService.show();
    // this.loaderService.display(true);
    const self = this;

    this.account.getUserToken(login).subscribe(
      resp => {
        if (resp.Succeeded) {
          const token = new Token();

          token.token = resp.Result.token;
          token.expiration = resp.Result.expiration;
          token.user = resp.Result.user;

          //// log into local storage
          // self.storage.token = token;
          // self.storage.user = token.user;
          localStorage.setItem(
            Constants.OAuthTokenKey,
            JSON.stringify(token.token)
          );
          localStorage.setItem(Constants.UserKey, JSON.stringify(token.user));
          // this.toastr.success('successfully sign in', 'success!');
          // self.loaderService.display(false);
          console.log(token.user.Role);
          this.router.navigate(["/dashboard"]);
          self.spinnerService.hide();
        } else {
          self.toastr.error(resp.Message, "Error!");
          // this.loaderService.display(false);
          self.spinnerService.hide();
          this.router.navigate(["/login"]);
        }
      },
      error => {
        console.log("login error");
        this.toastr.error("Failed to login " + error, "Error!");
        this.spinnerService.hide();
        this.router.navigate(["/login"]);
      }
    );
  }

  KeyPress($event) {
    // If Enter was Pressed
    if ($event.keyCode === 13) {
      // this.SignUp();
    }
  }

  public Redirect(role: string) {
    if (role === Constants.Roles.Administrator) {
      this.toastr.info(role);
      this.router.navigate([" "]);
    } else if (role === Constants.Roles.User) {
      this.router.navigate(["/login"]);
    }
  }
  public redirectTo() {
    this.router.navigate(["/forgotpass"]);
  }
}
