import { Component, OnInit } from '@angular/core';
import { PasswordChange } from '../shared/model';
import { ToastrService } from 'ngx-toastr';
import { Router } from '@angular/router';
import { StorageService } from '../service/storage.service';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { AccountService } from '../service/account.service';
import { isNull } from '@angular/compiler/src/output/output_ast';

@Component({
  selector: 'app-changepassword',
  templateUrl: './changepassword.component.html',
})
export class ChangePasswordComponent implements OnInit {

  baseurl: string = location.protocol + '//' + location.host + '/';
  public changePassword: PasswordChange = new PasswordChange();

  public password: string;
  constructor(private router: Router,
    private toastr: ToastrService, private storage: StorageService,
    private spinnerService: Ng4LoadingSpinnerService, private service: AccountService) {

  }


  ngOnInit() {
   // this.toastr.success('Hello world!', 'Toastr fun!');
  }

   public Ok(model: PasswordChange) {

    const self = this;

    if (model ===  null || model === undefined) {
      this.toastr.error('Password data cannot be empty');
    }
    if (model.NewPassword !== model.ConfirmPassword) {
      this.toastr.error('New Password must be the same with confirm password');
      }
      if (model.Password !== '')  {
        this.toastr.error('Password cannot be empty');
        }
    model.UserId = 0;

    this.spinnerService.show();
     self.service.ChangePasswordOk(model).subscribe(c => {

      if (c.Succeeded) {

        self.toastr.success('Operation successful');

        self.changePassword = new PasswordChange();
      } else {
        self.toastr.error('Operation failed ' + c.Message);
      }
      self.changePassword = new PasswordChange();
      self.spinnerService.hide();
     }, e => {

       console.log(e);

      self.toastr.error('Operation failed ' + e);
      self.spinnerService.hide();
     });


   }
}
