import { Injectable } from '@angular/core';
import { User, Token, UserLogin, Operation, PasswordChange, ResponseResource, UserResource, TransactionResponse, TransactionLogger, SearchModel } from '../shared/model';
import { DataService } from './data.service';
import { StorageService } from './storage.service';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { SequencePage } from '../shared/sequencepage';

@Injectable({
  providedIn: 'root'
})

export class TransactionService {

  constructor(private data: DataService, private storage: StorageService) {
  }

  public getTransactionSearch(model: SearchModel): Observable<Operation<SequencePage<TransactionResponse>>> {

    const url = `/api/v1/transaction/records`;

    const result = this.data.Post<SequencePage<TransactionResponse>>(url, model);

    return result;
  }
  public getTransaction(pageIndex: number, pageSize: number): Observable<Operation<SequencePage<TransactionResponse>>> {

    const url = `/api/v1/transaction/${pageIndex}/${pageSize}`;

    const result = this.data.Get<SequencePage<TransactionResponse>>(url);
    return result;
  }
  public getTransactionLog(pageIndex: number, pageSize: number): Observable<Operation<SequencePage<TransactionResponse>>> {

    const url = `/api/v1/transaction/${pageIndex}/logs/${pageSize}`;

    const result = this.data.Get<SequencePage<TransactionResponse>>(url);
    return result;
  }
  public getCreditTransaction(pageIndex: number, pageSize: number): Observable<Operation<SequencePage<TransactionResponse>>> {

    const url = `/api/v1/transaction/${pageIndex}/credit/${pageSize}`;

    const result = this.data.Get<SequencePage<TransactionResponse>>(url);
    return result;
  }
  public getDebitTransaction(pageIndex: number, pageSize: number): Observable<Operation<SequencePage<TransactionResponse>>> {

    const url = `/api/v1/transaction/${pageIndex}/debit/${pageSize}`;

    const result = this.data.Get<SequencePage<TransactionResponse>>(url);
    return result;
  }

  public getCustomerNameByCode(search: string): Observable<Operation<string>> {

    const url = `/api/v1/customer/${search}`;

    const result = this.data.Get<string>(url);
    return result;
  }

 public PostTransactionLog(model: TransactionLogger): Observable<Operation<TransactionLogger>> {

  const url = `/api/v1/transaction/log`;
  const result = this.data.Post<TransactionLogger>(url, model);

  return result;
 }
 public ApproveTransaction(id: number): Observable<Operation<TransactionLogger>> {

  const url = `/api/v1/transaction/approval/${id}`;
  const result = this.data.Get<TransactionLogger>(url);

  return result;
 }
 public DelineTransaction(id: number): Observable<Operation<TransactionLogger>> {

  const url = `/api/v1/transaction/decline/${id}`;
  const result = this.data.Get<TransactionLogger>(url);

  return result;
 }
}
