import { Component, OnInit } from "@angular/core";
import { Router, ActivatedRoute, ParamMap } from "@angular/router";
import { StorageService } from "../service/storage.service";
import { Ng4LoadingSpinnerService } from "ng4-loading-spinner";
import { ToastrService } from "ngx-toastr";
import {
  RecordStatus,
  WithdrawalResponse,
  WithdrawApprovalModel,
  SearchModel
} from "../shared/model";
import { SequencePage } from "../shared/sequencepage";
import { WithdrawalService } from "../service/withdrawal.service";
import { switchMap } from "rxjs/operators";

@Component({
  selector: "app-withdrawal",
  templateUrl: "./withdrawal.component.html"
  // styleUrls: ['./customer.component.css']
})
export class WithdrawalComponent implements OnInit {
  baseurl: string = location.protocol + "//" + location.host + "/";
  public data: SequencePage<WithdrawalResponse>;
  public response: WithdrawalResponse[] = new Array<WithdrawalResponse>();
  public pageIndex = 1;
  public pageSize = 100;
  public pageLinks: number[] = [];
  public hasPage: boolean;
  public withd: WithdrawApprovalModel = new WithdrawApprovalModel();
  public roleName: string;

  public searchModel: SearchModel = new SearchModel();

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private toastr: ToastrService,
    private storage: StorageService,
    private spinnerService: Ng4LoadingSpinnerService,
    private service: WithdrawalService
  ) {}

  public reloadPage() {
    document.location.reload();
  }

  ngOnInit() {
    this.roleName = this.storage.GetUserInfo().Role;
    this.LoadFirstPage();
  }

  LoadFirstPage() {
    const get = this.Get(0);
    this.hasPage = this.HasNoPage();
    return get;
  }

  LoadLastPage() {
    if (this.data) {
      return this.Get(Math.ceil(this.data.SequenceLength / this.pageSize) - 1);
    }
  }

  HasNoPage(): boolean {
    const result: boolean =
      this.data == null ||
      typeof this.data === "undefined" ||
      this.data.Page.length === 0;

    return result;
  }

  public ShowApprovalForm(id: number) {
    // const wid = this.route.snapshot.paramMap.get('id');

    this.router.navigate(["/withdrawalagent", id]);
  }

  public Get(pageIndex?: number): Array<WithdrawalResponse> {
    const self = this;

    self.service.getWithdrawal(pageIndex, this.pageSize).subscribe(
      resp => {
        const _page = resp.Result;

        if (_page.PageSize === 0) {
          // <-- debug stuff
          self.pageLinks = [];
        } else {
          self.data = new SequencePage<WithdrawalResponse>(
            _page.Page,
            _page.SequenceLength,
            _page.PageSize,
            _page.PageIndex
          );
          self.pageLinks = self.data.AdjacentIndexes(2);
          self.response = self.data.Page;
        }
        return self.response;
      },
      err => {
        self.data = new SequencePage<WithdrawalResponse>([], 0);
        self.pageLinks = self.data.AdjacentIndexes(2);
        return self.response;
      }
    );
    return self.response;
  }

  public GetWithdrawalStatus(status: number): string {
    if (status === 0) {
      return "Pending";
    } else if (status === 1) {
      return "Approved";
    } else if (status === 2) {
      return "Paid";
    }
  }
  // WithdrawalDecline
  public ApproveWithdrawal(id: number) {
    const self = this;
    this.withd.WithdrawalId = id;
    this.withd.AgentUserId = 0;

    this.service.WithdrawalApproval(this.withd).subscribe(
      c => {
        if (c.Succeeded) {
          self.LoadFirstPage();
          self.toastr.success("Operation Successful");
        } else {
          self.toastr.error(c.Message);
        }
      },
      e => {
        self.toastr.error("Operation failed " + e);
      }
    );
  }

  public DeclineWithdrawal(id: number) {
    const self = this;
    if (!confirm("Are you sure")) {
      self.toastr.success("operation cancelled successfully", "Success");
      return;
    }
    this.service.WithdrawalDecline(id).subscribe(
      c => {
        if (c.Succeeded) {
          self.LoadFirstPage();
          self.toastr.success("Operation Successful");
        } else {
          self.toastr.error(c.Message);
        }
      },
      e => {
        self.toastr.error("Operation failed " + e);
      }
    );
  }

  public SearchWithdrawal(searchMod: SearchModel) {
    const self = this;

    self.service.getWithdrawalSearch(searchMod).subscribe(
      resp => {
        const _page = resp.Result;
        if (_page.PageSize === 0) {
          // <-- debug stuff
          self.pageLinks = [];
        } else {
          self.data = new SequencePage<WithdrawalResponse>(
            _page.Page,
            _page.SequenceLength,
            _page.PageSize,
            _page.PageIndex
          );
          self.pageLinks = self.data.AdjacentIndexes(2);
          self.response = self.data.Page;
        }
        return self.response;
      },
      err => {
        self.data = new SequencePage<WithdrawalResponse>([], 0);
        self.pageLinks = self.data.AdjacentIndexes(2);
        return self.response;
      }
    );
    return self.response;
  }
}
