import { Component, OnInit, AfterViewInit, Inject } from "@angular/core";
import { Router } from "@angular/router";
import { StorageService } from "../service/storage.service";
import { Ng4LoadingSpinnerService } from "ng4-loading-spinner";
import { ToastrService } from "ngx-toastr";
import { Constants } from "../shared/Constants";
import * as $ from "jquery";
import { ReportService } from "../service/report.service";
import {
  Dashboard,
  DashboardModel,
  Customer,
  RecordStatus,
  TransactionResponse,
  SmsNotificationModel
} from "../shared/model";
import { Observable } from "rxjs";
import { SequencePage } from "../shared/sequencepage";
import { TransactionService } from "../service/transaction.service";

@Component({
  selector: "app-smsdebit",
  templateUrl: "./smsdebit.component.html"
  // styleUrls: ['./customer.component.css']
})
export class SmsDebitComponent implements OnInit {
  baseurl: string = location.protocol + "//" + location.host + "/";
  public data: SequencePage<TransactionResponse>;
  public transactions: TransactionResponse[] = new Array<TransactionResponse>();
  public pageIndex = 1;
  public pageSize = 100;
  public pageLinks: number[] = [];
  public hasPage: boolean;

  public smsmodel: SmsNotificationModel = new SmsNotificationModel();

  constructor(
    private router: Router,
    private toastr: ToastrService,
    private storage: StorageService,
    private spinnerService: Ng4LoadingSpinnerService,
    private service: TransactionService,
    private report: ReportService
  ) {}

  ngOnInit() {
    this.GetSmsNotification();
    this.LoadFirstPage();
  }

  public GetStatus(status: RecordStatus) {}
  LoadFirstPage() {
    const get = this.Get(0);
    this.hasPage = this.HasNoPage();
    return get;
  }

  public LoadLastPage() {
    if (this.data) {
      return this.Get(Math.ceil(this.data.SequenceLength / this.pageSize) - 1);
    }
  }

  public HasNoPage(): boolean {
    const result: boolean =
      this.data == null ||
      typeof this.data === "undefined" ||
      this.data.Page.length === 0;

    return result;
  }

  public Get(pageIndex?: number): Array<TransactionResponse> {
    const self = this;

    self.service.getTransaction(pageIndex, this.pageSize).subscribe(
      resp => {
        const _page = resp.Result;

        if (_page.PageSize === 0) {
          // <-- debug stuff
          self.pageLinks = [];
        } else {
          self.data = new SequencePage<TransactionResponse>(
            _page.Page,
            _page.SequenceLength,
            _page.PageSize,
            _page.PageIndex
          );
          self.pageLinks = self.data.AdjacentIndexes(2);
          self.transactions = self.data.Page;
        }
        return self.transactions;
      },
      err => {
        self.data = new SequencePage<TransactionResponse>([], 0);
        self.pageLinks = self.data.AdjacentIndexes(2);
        return self.transactions;
      }
    );
    return self.transactions;
  }

  public GetSmsNotification() {
    const self = this;

    self.report.GetSmsNotification().subscribe(c => {
      self.smsmodel = c.Result;
    });
  }
}
