import { Component, OnInit, AfterViewInit, Inject } from "@angular/core";
import { Router } from "@angular/router";
import { StorageService } from "../service/storage.service";
import { Ng4LoadingSpinnerService } from "ng4-loading-spinner";
import { ToastrService } from "ngx-toastr";
import { Constants } from "../shared/Constants";
import * as $ from "jquery";
import { ReportService } from "../service/report.service";
import { Dashboard, DashboardModel } from "../shared/model";
import { HttpRequest } from "@angular/common/http";

@Component({
  selector: "app-dashboard",
  templateUrl: "./dashboard.component.html",
  styleUrls: ["./dashboard.component.css"]
})
export class DashboardComponent implements OnInit, AfterViewInit {
  baseurl: string = location.protocol + "//" + location.host + "/";

  dashboard: DashboardModel = new DashboardModel();

  constructor(
    private request: HttpRequest<any>,
    private router: Router,
    private toastr: ToastrService,
    private storage: StorageService,
    private spinnerService: Ng4LoadingSpinnerService,
    private report: ReportService
  ) {
    // , @Inject(DOCUMENT) private document: any
  }

  ngAfterViewInit(): void {}

  ngOnInit() {
    try {
      this.spinnerService.show();
      const self = this;
      // const token = this.storage.Token;
      const _token = localStorage.getItem(Constants.OAuthTokenKey);

      if (_token != null) {
        self.GetDashboard();
        this.router.navigate([""]);
      } else {
        this.router.navigate(["/login"]);
      }
      this.spinnerService.hide();
    } catch (e) {
      this.spinnerService.hide();
      console.log(this.baseurl + "#/login");
      document.location.href = this.baseurl + "#/login";
      this.router.navigate(["/login"]);
    }
  }

  public GetDashboard() {
    const self = this;
    this.report.GetDashboard().subscribe(c => {
      self.dashboard = c.Result;
    });
  }

  public GetWithdrawalStatus(status: number): string {
    if (status === 0) {
      return "Pending";
    } else if (status === 1) {
      return "Approved";
    } else if (status === 2) {
      return "Paid";
    }
  }
}
