import { Component, OnInit } from '@angular/core';
import { User } from '../shared/model';
import { SequencePage } from '../shared/sequencepage';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { AccountService } from '../service/account.service';
import { StorageService } from '../service/storage.service';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
})
export class ProfileComponent implements OnInit {

   baseurl: string = location.protocol + '//' + location.host + '/';

   user: User = new User();
   constructor(private router: Router,
   private toastr: ToastrService, private storage: StorageService,
   private spinnerService: Ng4LoadingSpinnerService, private service: AccountService) {

  }



  ngOnInit() {

     const self = this;

     self.service.getCurrentUser().subscribe(c => {
     self.user.Address = c.Result.Address;
     self.user.EmailAddress = c.Result.EmailAddress;
     self.user.Mobile = c.Result.Mobile;
     self.user.FirstName = c.Result.FirstName;
     self.user.LastName = c.Result.LastName;
     });
  }

   Ok(model: User) {

     const self = this;

    this.service.updateWebProfile(model).subscribe(c => {

      if (c.Succeeded) {
        self.toastr.success('Operation successful');
      } else {
        self.toastr.error('Operation failed');
      }
    });

   }

}
