import { Injectable } from "@angular/core";
import {
  User,
  Token,
  UserLogin,
  Operation,
  PasswordChange,
  ResponseResource,
  UserResource,
  DashboardModel,
  SmsNotificationModel
} from "../shared/model";
import { DataService } from "./data.service";
import { StorageService } from "./storage.service";
import { Router } from "@angular/router";
import { Observable } from "rxjs";
import { SequencePage } from "../shared/sequencepage";

@Injectable({
  providedIn: "root"
})
export class ReportService {
  constructor(private data: DataService, private storage: StorageService) {}

  // user registration
  public GetDashboard(): Observable<Operation<DashboardModel>> {
    const url = `/api/v1/report/dashboard`;
    const r = this.data.Get<DashboardModel>(url);
    return r;
  }

  public GetSmsNotification(): Observable<Operation<SmsNotificationModel>> {
    const url = `/api/v1/settlement/sms-notify`;
    const r = this.data.Get<SmsNotificationModel>(url);
    return r;
  }
}
