import { Component, OnInit } from '@angular/core';
import { SequencePage } from '../shared/sequencepage';
import { User, SearchModel } from '../shared/model';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { StorageService } from '../service/storage.service';
import { AccountService } from '../service/account.service';

@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
})
export class UsersComponent implements OnInit {

  baseurl: string = location.protocol + '//' + location.host + '/';
  public data: SequencePage<User>;
  public response: User[] = new Array<User>();
  public pageIndex = 1;
  public pageSize = 100;
  public pageLinks: number[] = [];
  public hasPage: boolean;

  public searchModel: SearchModel = new SearchModel();

  constructor(private router: Router,
    private toastr: ToastrService, private storage: StorageService,
    private spinnerService: Ng4LoadingSpinnerService, private service: AccountService) {

  }

  public reloadPage() {
    document.location.reload();
  }

  ngOnInit() {
    this.LoadFirstPage();
  }


    LoadFirstPage() {
      const get = this.Get(0);
      this.hasPage = this.HasNoPage();
      return get;
    }

    LoadLastPage() {

      if (this.data) {
        return this.Get((Math.ceil(this.data.SequenceLength / this.pageSize) - 1));
      }
    }

    HasNoPage(): boolean {
      const result: boolean = this.data == null
      || typeof this.data === 'undefined'
      || this.data.Page.length === 0;

      return result;
    }

    public Get(pageIndex?: number): Array<User> {

      const self = this;

      self.service.getUserList(pageIndex, this.pageSize).subscribe(resp => {
        const _page = resp.Result;

        if (_page.PageSize === 0) {
          // <-- debug stuff
          self.pageLinks = [];
        } else {
          self.data = new SequencePage<User>(_page.Page, _page.SequenceLength, _page.PageSize, _page.PageIndex);
          self.pageLinks = self.data.AdjacentIndexes(2);
          self.response = self.data.Page;
        }
        return self.response;
      }, err => {
        self.data = new SequencePage<User>([], 0);
        self.pageLinks = self.data.AdjacentIndexes(2);
        return self.response;
      });
      return self.response;
    }

    public SearcUsersData(searchMod: SearchModel) {

      const self = this;

      self.service.getUserAdminList(searchMod).subscribe(resp => {
        const _page = resp.Result;
        if (_page.PageSize === 0) {
          // <-- debug stuff
          self.pageLinks = [];
        } else {
          self.data = new SequencePage<User>(_page.Page, _page.SequenceLength, _page.PageSize, _page.PageIndex);
          self.pageLinks = self.data.AdjacentIndexes(2);
          self.response = self.data.Page;
        }
        return self.response;
      }, err => {
        self.data = new SequencePage<User>([], 0);
        self.pageLinks = self.data.AdjacentIndexes(2);
        return self.response;
      });
      return self.response;
    }
}
