import { Component, OnInit} from '@angular/core';
import { Router , ActivatedRoute } from '@angular/router';
import { StorageService } from '../service/storage.service';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { ToastrService } from 'ngx-toastr';
import { DashboardModel, Customer, RecordStatus } from '../shared/model';
import { SequencePage } from '../shared/sequencepage';
import { CustomerService } from '../service/customer.service';
import { HttpEventType, HttpRequest, HttpClient, HttpResponse } from '@angular/common/http';

@Component({
  selector: 'app-updatecustomer',
  templateUrl: './updatecustomer.component.html',
  // styleUrls: ['./customer.component.css']
})
export class UpdateCustomerComponent implements OnInit  {

  baseurl: string = location.protocol + '//' + location.host + '/';
  recordStatus: RecordStatus;
  dashboard: DashboardModel = new DashboardModel();

  public data: SequencePage<Customer>;
  public customers: Customer[] = new Array<Customer>();
  public pageIndex = 1;
  public pageSize = 100;
  public pageLinks: number[] = [];
  public hasPage: boolean;
 public customerId: number;
 public customer: Customer = new Customer();


  public formData: FormData = new FormData();
  public uploadProgress: number;
  public fileName: string;

  public sformData: FormData = new FormData(); // signature
  public suploadProgress: number; // signature
  public sfileName: string; // signature


  constructor(private router: Router, private route: ActivatedRoute,
    private toastr: ToastrService, private storage: StorageService,
    private spinnerService: Ng4LoadingSpinnerService,
    private service: CustomerService, private http: HttpClient) {
      const cid = this.route.snapshot.paramMap.get('id');

      if (cid !== null) {
        this.customerId = Number(cid);
  }
}

 ngOnInit() {

  const self = this;
  this.service.GetCustomer(this.customerId).subscribe(c => {
     if (c.Succeeded) {
       self.customer = c.Result;
     } else {
       self.toastr.error(c.Message, 'Error ');
     }
  }, e => {
   self.toastr.error('Operation failed ' + e , 'Error' );
  });
 }

  onsignatureFileChanged(files: any) {
    if (!files) {
      this.toastr.error('Error, please check the file inputst');
      return;
    }
    for (const file of files) {
      this.formData.append('signature', file);
      this.fileName = file.name;
    }
  }

  onFileChanged(files: any) {
    if (!files) {
      this.toastr.error('Error, please check the file inputst');
      return;
    }
    for (const file of files) {
      this.formData.append('picture', file);
      this.fileName = file.name;
    }
  }
public Update() {

   const self = this;
   this.service.UpdateCustomer(this.customer).subscribe(c => {

     if (c.Succeeded) {
       // Picture upload
       const req = new HttpRequest('POST', `api/v1/customer/image-upload/${c.Result.CustomerId}`, self.formData, {
         reportProgress: true,
       });

       self.http.request(req).subscribe(event => {

         self.formData = new FormData();

         if (event.type === HttpEventType.UploadProgress) {
           self.uploadProgress = Math.round(100 * event.loaded / event.total);
           this.spinnerService.show();

         } else if (event instanceof HttpResponse) {
           console.log('Files uploaded!');
         }
         self.formData = new FormData();
         this.spinnerService.hide();
         // self.toastr.success('Operation successful');
       });

       self.toastr.success('Operation successful');
     } else {
       self.toastr.error(c.Message, 'Error');
     }
     self.formData = new FormData();
   }, e => {
    self.toastr.error('Operation failed' +  e, 'Error');
   });

}



}
