import {
  Component,
  Injectable,
  PLATFORM_ID,
  Inject,
  OnInit
} from "@angular/core";
import { Constants } from "../shared/Constants";
import { User, Token } from "../shared/model";
import { isPlatformBrowser, isPlatformServer } from "@angular/common";

@Injectable({
  providedIn: "root"
})
export class StorageService implements OnInit {
  public user: User = new User();
  public token: Token = new Token();

  constructor() {}

  public GetToken(): Token {
    const token = localStorage.getItem(Constants.OAuthTokenKey);
    const _toks = <Token>JSON.parse(token);
    return _toks;
  }
  public GetUserInfo(): User {
    const _user = localStorage.getItem(Constants.UserKey);
    this.user = <User>JSON.parse(_user);
    return this.user;
  }

  ngOnInit() {}

  get User() {
    if (!this.user) {
      this.user = this.Get<User>(Constants.UserKey);
    }
    return this.user;
  }

  set User(user: User) {
    this.user = user;
    this.Store(Constants.UserKey, user);
  }

  get Token() {
    if (!this.token) {
      this.token = this.Get<Token>(Constants.OAuthTokenKey);
    }
    return this.token;
  }

  set Token(token: Token) {
    this.Store(Constants.OAuthTokenKey, token);
  }

  // Local Storage Methods
  private Store(key, value) {
    return localStorage.setItem(key, JSON.stringify(value));
  }

  private Remove(key: string) {
    localStorage.removeItem(key);
  }

  private Get<T>(key: string) {
    return <T>JSON.parse(localStorage.getItem(key));
  }

  Clear() {
    localStorage.clear();
  }
}
