import { Injectable } from '@angular/core';
import { User, Token, UserLogin, Operation, PasswordChange, UserResource, AdminUser, SearchModel } from '../shared/model';
import { DataService } from './data.service';
import { StorageService } from './storage.service';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { SequencePage } from '../shared/sequencepage';

@Injectable({
  providedIn: 'root'
})

export class AccountService {

  constructor(private data: DataService, private storage: StorageService) {
  }

  // Get token
  public getUserToken(user: UserLogin): Observable<Operation<Token>> {
    const url = '/api/v1/account/login';
    const result = this.data.PostWithOutToken<Token>(url, user);
    return result;
  }

  // user registration
  public userLogin(model: UserLogin): Observable<Operation<User>> {
    const url = `/api/v1/account/register`;
    const r = this.data.Post<User>(url, model);
    return r;
  }


  public getUsers(): Observable<Operation<User[]>> {
    return this.data.Get<User[]>('/api/account/users');
  }

  // get user by user id
  public getUser(userId: number): Observable<Operation<User>> {

    const url = `/api/v1/account/user/${userId}`;
    const r = this.data.Get<User>(url);
    return r;
  }


    // get user by user id
    public getCurrentUser(): Observable<Operation<User>> {

      const url = `/api/v1/account/current-user`;
      const r = this.data.Get<User>(url);
      return r;
    }

  // delete funtnx
  public deleteUser(id: number): Observable<Operation<User>> {
    const url = '/api/v1/account/' + id;
    return this.data.Delete<User>(url);
  }

  // register user
  public registerUser(model: User): Observable<Operation<User>> {
    const url = '/api/v1/account/register';
    return this.data.Post<User>(url, model);
  }

  // update profile
  public updateProfile(model: User): Observable<Operation<User>> {
    const url = `/api/v1/account/update-profile`;
    return this.data.Put<User>(url, model);
  }
    // update profile
    public updateWebProfile(model: User): Observable<Operation<User>> {
      const url = `/api/v1/account/update-userprofile`;
      return this.data.Put<User>(url, model);
    }
  // change password ..
  public changepassword(model: PasswordChange) {
    const url = `/api/v1/account/change-password`;
    return this.data.Put<User>(url, model);
  }

  public  registerCentralAdmin(model: UserResource) {
    const url = `/api/v1/account/register`;
    return this.data.Post<User>(url, model);
  }

  // get user list..
  public getRegisterUser(pageIndex: number, pageSize: number):
   Observable<Operation<SequencePage<UserResource>>> {
    const url = `/api/v1/account/${pageIndex}/all/${pageSize}`;
    const result = this.data.Get<SequencePage<UserResource>>(url);
    return result;
  }

  // get user list..
  public getUserList(pageIndex: number, pageSize: number):
   Observable<Operation<SequencePage<User>>> {
    const url = `/api/v1/account/${pageIndex}/${pageSize}`;
    const result = this.data.Get<SequencePage<User>>(url);
    return result;
  }

   // get user list..
   public getUserAdminList(model: SearchModel): Observable<Operation<SequencePage<User>>> {
    const url = `/api/v1/account/customer-users`;
    const result = this.data.Post<SequencePage<User>>(url, model);
    return result;
  }

  public getAgentUserdata(pageIndex: number, pageSize: number): Observable<Operation<SequencePage<User>>> {
   const url = `/api/v1/account/${pageIndex}/agent/${pageSize}`;
   console.log(url);
   const result = this.data.Get<SequencePage<User>>(url);
   console.log(result);
   return result;
 }

 public ChangePasswordOk(model: PasswordChange): Observable<Operation<PasswordChange>> {
  const url = `/api/v1/account/change-password`;
  const result = this.data.Put<PasswordChange>(url, model);
  return result;
 }
 public GetAdminUser(): Observable<Operation<User[]>> {

  const url = `/api/v1/account/admin-users-data`;
  const result = this.data.Get<User[]>(url);
  return result;
 }

 public GetAdminUserPaged(pageIndex: number, pageSize: number): Observable<Operation<SequencePage<User>>> {

  const url = `/api/v1/account/admin-users-data/${pageIndex}/${pageSize}`;
  const result = this.data.Get<SequencePage<User>>(url);
  return result;
 }
 public GetAdminUserSearch(model: SearchModel): Observable<Operation<SequencePage<User>>> {

  const url = `/api/v1/account/admin-records`;

  const result = this.data.Post<SequencePage<User>>(url, model);
  return result;
 }
 public CreateUser(model: AdminUser): Observable<Operation<AdminUser>>  {
  const url = `/api/v1/account/register-user`;
   model.IsActive = true;

  const result = this.data.Post<AdminUser>(url, model);

  return result;
 }
}
